body {
  margin: 0;
  background-color: #fff;
  color: #000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  padding: 20px;
}

h2 {
  margin-bottom: 6px;
}

button {
  font-size: 1.2rem;
  margin-right: 6px;
  margin-bottom: 6px;
}

button.selected {
  background-color: aqua;
}

ol {
  font-size: 1.2rem;
}

ol li span {
  padding: 5px 20px;
  margin: 4px 0;
  display: inline-block;
  cursor: pointer;
  background-color: beige;
  border-radius: 5px;
}
ol li span:hover {
  text-decoration: underline;
}

.selected-activity .tags {
  padding: 0;
}

.selected-activity .tags li {
  display: inline-block;
  background-color: #0002;
  padding: 2px 8px;
  margin-right: 8px;
  margin-bottom: 5px;
  border-radius: 5px;
}

.selected-activity .json {
  color: #0003;
}

.select-random-activity-button {
  background-color: transparent;
  border: none;
  position: fixed;
  right: 0px;
  bottom: 20px;
  cursor: pointer;
}

.select-random-activity-button img {
  width: 200px;
}

.tag-button {
  cursor: pointer;
}

.feedback-request {
  margin-top: 70px;
  max-width: 800px;
}

.like-button {
  text-align: center;
}

.like-button span {
  font-size: 5rem;
  color: #ffdad4;
  cursor: pointer;
  transition: color .4s ease-in-out;
}

.like-button span:hover {
  color: tomato;
}

.like-button.liked span {
  color: tomato;
  cursor: default;
}

.refresh-filters {
  position: fixed;
  font-size: 3rem;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
